@font-face {
  font-family: "nyt-franklin";
  src: url("./fonts/franklin-normal-400.woff2") format("woff2"),url("./fonts/franklin-normal-400.woff2") format("woff");
  font-weight: 400;
  font-style: normal
}
@font-face {
  font-family: "nyt-karnakcondensed";
  src: url("./fonts/karnakcondensed-normal-700.woff2") format("woff2"),url("./fonts/karnakcondensed-normal-700.woff2") format("woff");
  font-weight: 700;
  font-style: normal
}



body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
